import React from 'react'
import {Sdata} from './Sdata'
import './index.css'

const App = () => {
    return(
        <> 
            <h1 className='heading'>Top International Games</h1>
            <div className='flex-container'> 
                {Sdata.map((val)=>{
                    return(
                        <Card key={val.id} imgsrc={val.imgsrc} category={val.category} game={val.game} link={val.link} />
                    )
                })}
            </div>
        </>
    );
};

const Card = (props) => {
    return(
        <div>
            <img src={props.imgsrc} alt="game img"/>
            <div>
                <span>{props.category}</span>
                <h3>{props.game}</h3>
                <a href={props.link}>
                    <button className='card-btn'>VIEW</button>
                </a>
            </div>
        </div>
    );
};

export default App;